.bright-pointer {
  color: white;
  font-weight: 700;
  font-family: 'Playfair Display', serif;
  font-size: 1.08rem;
  letter-spacing: -0.2px;
  font-style: italic;
  margin-bottom: 0.5rem;
  text-align: left;
  display: flex;
  align-items: center;
}
.bright-pointer .bullet {
  font-weight: 800;
  color: white;
  margin-right: 6px;
  font-size: 1.2em;
}
.home-container {
  position: relative;
  background-image: url('../assets/court-background.png');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  margin: 0 auto;
  min-width: 100vw;
  overflow-x: hidden;
}


/* Remove global blur from viewport */
.home-container::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  pointer-events: none;
}


.home-section {
  position: relative;
  z-index: 2;
  background: transparent;
  margin: 40px auto 30px auto;
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  max-width: 1200px;
}

.hero-section-wrapper {
  backdrop-filter: blur(4px);
}
/* Custom thin capsule-shaped scrollbar for all sections */
.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #f8b217 #222;
}
.custom-scrollbar::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  background: #f8b217;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  background: #f8b217;
  border-radius: 10px;
  opacity: 1.5;
  min-height: 24px;
}
.custom-scrollbar::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}




.home-section:last-child {
  margin-bottom: 0;
  border-radius: 20px 20px 0 0;
}



.journey-section-wrapper {
  border-left: 5px solid #f8b217;
  padding: 40px 0px;
  min-width: 100%;
  border-radius: 0px;
  background-image: url('../assets/front/images/bgbanner.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.journey-section-wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: inherit;
  background-size: inherit;
  background-position: inherit;
  background-repeat: inherit;
  opacity: 0.75;
  z-index: 0;
  pointer-events: none;
}

.journey-section-wrapper > * {
  position: relative;
  z-index: 1;
}

.inventory-section-wrapper {
  /* background: rgba(255, 255, 255, 0.98); */
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
  padding: 20px 0px;
  border: none;
}

.updates-section-wrapper {
  /* background: linear-gradient(145deg, rgba(240, 248, 255, 0.9), rgba(255, 255, 255, 0.9)); */
   box-shadow: 0 12px 40px rgba(0, 0, 0, 0.15);
}


.features-section-wrapper {
  padding: 20px 0px;
  background: rgba(250, 250, 250, 0.95);
  border-top: 3px solid #f8b217;
  border-bottom: 3px solid #f8b217;
}


.stats-section-wrapper {
  background: linear-gradient(135deg, rgba(248, 178, 23, 0.05), rgba(255, 255, 255, 0.95));
  margin: 50px auto;
  margin-bottom: 80px;
  margin-top: 80px;
}


.steps-section-wrapper {
  background: rgba(248, 251, 255, 0.9);
  border-radius: 30px !important;
  margin-bottom: 80px;
}


.final-cta-section-wrapper {
  background: linear-gradient(135deg, rgba(248, 178, 23, 0.15), rgba(255, 255, 255, 0.9));
  border: 2px solid rgba(248, 178, 23, 0.4);
}

@media (max-width: 768px) {
  .home-container {
    max-width: 100%;
    background-attachment: scroll;
  }

  .home-section {
    margin: 10px;
    padding: 20px 15px;
    border-radius: 15px;
  }
}

@media (max-width: 480px) {
  .home-section {
    margin: 5px;
    padding: 15px 10px;
    border-radius: 10px;
  }
}
